import React from 'react'
import { Link } from 'gatsby'
import pic from '../images/typesCTF.webp'

import Layout from '../components/layout'

const typeCTF = () => (
  <Layout>
    <h1>How many types of CTF are there?</h1>

    <article>
      <span className="image main">
        <img src={pic} alt="" height="350" />
      </span>
      <ul>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Just like cybersecurity, CTF has multiple domains. These domains
          contain multiple sub-challenges, sometimes inspired by real-life
          events. A few are as follows:
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Physical challenges: In a physical challenge, the challenger will give
          you a key and lock you in a closed space. This key can be any storage
          device. The challenge is to examine the key, find the flag, and break
          out of the space.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Website challenges: A webpage or a set of pages are given to you for
          finding a flag. The challenge might involve exploring the page and
          crafting a file that makes the website output the flag.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Network challenges: A sample network is given, and it is under attack.
          To save the network, you need to find the flag and perform the
          mentioned tasks with it.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Reverse engineering challenges: In a reverse engineering challenge,
          you have to scour through the code of an executable file to find the
          flag. For establishing a better understanding of the challenge, you
          may need to run the executable and understand its behavior.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Steganography challenges: In this case, the flag is hidden in an image
          or some other similar media. Again your task here is to look through
          the media to find the flag.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Cryptographic challenges: The challenger encrypts the flag using some
          technique, adds some information about the encryption method used, and
          encrypts it again. This process may be repeated again and again to
          increase complexity. You have to decrypt each layer one-by-one to
          reach the flag.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Forensic analysis challenges: You are provided with logs and memory
          dumps of a compromised machine or a machine that was used to attack
          another computer. You will find the flag in the log files or the
          memory dumps, after solving a few challenges.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Mobile security challenges: A compromised smartphone is provided to
          you. The challenge here is to find the compromised application and use
          it to find the flag.
        </li>
        <li style={{marginBottom: '40px'}}>
          {' '}
          Break-in challenges: Probably the hardest and the most popular set of
          challenges on this list. You are given a virtual machine, and you have
          to hack into the machine and gain root access to find the flag.
        </li>
      </ul>
    </article>
    <br />
    <Link to="/">Go back to the homepage</Link>
    <br />
  </Layout>
)

export default typeCTF
