import React from 'react'
import { Link } from 'gatsby'
import pic from '../images/pic02.webp'

import Layout from '../components/layout'

const cyberera = () => (
  <Layout>
    <h1>Introduction to CTF</h1>
    <article>
      <h3 className="major">An intro</h3>
      <p>
        Close your eyes and picture a treasure hunt. What do you see? A bunch of
        teams competing to find a treasure? If you think so, you are right. Let
        me put "CTF" into simpler terms. CTF stands for Capture The Flag. It's a
        treasure hunt, and it happens inside a machine. You race against other
        teams to find the treasure. What treasure are you looking for? It's
        called a flag. A flag is a variable that defines the current state of
        the system or holds an intermediary value. Find this flag first, and you
        win the game.
      </p>
      <span className="image main">
        <img src={pic} alt="" height="400" />
      </span>
    </article>
    <br />
    <Link to="/">Go back to the homepage</Link>
    <br />
  </Layout>
)

export default cyberera
