import React from 'react'
import { Link } from 'gatsby'
import pic from '../images/pic01.webp'

import Layout from '../components/layout'

const ethics = () => (
  <Layout>
    <h1>The utility of CTFs</h1>

    <article>
      <span className="image main">
        <img src={pic} alt="" height="350" />
      </span>
      <p>
        Despite an abundance of retaliation (from parents) to playing video
        games, there is enough evidence to show that gaming develops hand-eye
        coordination. Why is that? It's because you are emulating a physical
        activity, and your brain is getting the same training if you were
        engaged in physical activity. The same goes for a CTF event. When
        playing CTFs, you train your brain for real-life penetration testing
        challenges that you might face. Also, it helps build problem-solving
        skills, the highest in-demand skill on the market. In class, you learn
        the five stages of ethical hacking. In a CTF, you apply the five stages.
        It is the best approach to learning these concepts.
      </p>
    </article>
    <br />
    <Link to="/">Go back to the homepage</Link>
    <br />
  </Layout>
)

export default ethics
