import React from 'react'
import { Link } from 'gatsby'
import pic from '../images/usingCTF.webp'

import Layout from '../components/layout'

const usingCTF = () => (
  <Layout>
    <h1>Using CTFs to find what you love most in cybersecurity</h1>

    <article>
      <p>
        Just like food, no two people have the same taste in cybersecurity. You
        will need to experiment with concepts and practicals in the subject and
        find what fits your taste. A CTF event is probably the best tool out
        there to help you find that. Each CTF challenge is designed to test
        multiple concepts in the subject. If you become part of a team, you will
        concentrate on specific topics easily. It will take some time to reach
        there, but eventually, you will. Even if you can't find a specific
        stream to work with, experimenting with CTFs will help you gain more
        knowledge on the subject.
      </p>
      <span className="image main">
        <img src={pic} alt="" height="350" />
      </span>
    </article>
    <br />
    <Link to="/">Go back to the homepage</Link>
    <br />
  </Layout>
)

export default usingCTF
